export default class ColumnMiddleware {

	/**
	 * @param {Columns} model
	 * @param {Object} config
	 */
	static map( model, config ) {

		Object.keys( config ).forEach( ( column_name ) => {
			let editing_settings = config[ column_name ];
			let settings = model.get( column_name );

			settings.editable = editing_settings.editable;
			settings.type = editing_settings.type;
		} );

		return model;
	}

}